import { Formik, Form } from 'formik';
import { useDispatch, useSelector } from 'react-redux';
import { changePasswordFromPanelHandler } from '../../../redux/features/userInfo';
import * as Yup from 'yup';
import Input from '../../forms/Input';
import Toast from '../../../toasts/toasts';

const EditAccount = () => {
   const dispatch = useDispatch();
   // const password = useSelector((state) => state.user.userInfo.password);

   const initialValues = {
      oldPassword: '',
      password: '',
      confirmPassword: '',
   };
   const validationSchema = Yup.object({
      oldPassword: Yup.string().required('     '),
      password: Yup.string()
         .required('    ')
         .min(8, '     8  '),
      confirmPassword: Yup.string()
         .required('    ')
         .oneOf([Yup.ref('password'), ''], '     '),
   });

   const onSubmit = (value) => {
      dispatch(changePasswordFromPanelHandler(value));
      console.log(value);
   };

   return (
      <div class='pas-conainer'>
         <div className='change-pas-img'>
            <img src='../images/Forgot-password.gif' alt='' />
         </div>
         <div class='forgot-password'>
            <h1 class='txt-pass'>  </h1>
            <Formik
               initialValues={initialValues}
               validationSchema={validationSchema}
               onSubmit={onSubmit}>
               <Form>
                  <Input
                     className='password-input'
                     type='password'
                     placeholder=' '
                     name='oldPassword'
                  />
                  <Input
                     className='password-input'
                     type='password'
                     placeholder='  '
                     name='password'
                  />
                  <Input
                     className='password-input'
                     type='password'
                     placeholder='  '
                     name='confirmPassword'
                  />
                  <div className='pas-btn-container'>
                     <button type='submit' className='change-pas-btn'>
                        
                     </button>
                  </div>
               </Form>
            </Formik>
         </div>
      </div>
   );
};

export default EditAccount;
