import * as Yup from 'yup';

const useValidation = (action) => {
   let validationSchema;

   const options = ['', '', ''];

   const firstName = Yup.string().required('    ');
   const lastName = Yup.string().required('    ');
   const phoneNumber = Yup.string()
      .required('     ')
      .matches(
         /^(\+98|0098|98|0)?9\d{9}$/g,
         '      '
      );
   const nationalCode = Yup.string().required('      ');
   const grade = Yup.string()
      .required('    ')
      .oneOf(options);
   const password = Yup.string()
      .required('    ')
      .min(8, '     8  ');
   const confirmPassword = Yup.string()
      .required('    ')
      .oneOf([Yup.ref('password'), ''], '     ');
   const code = Yup.string()
      .required('    ')
      .min(4, '       4  ');

   switch (action) {
      case 'register':
         validationSchema = Yup.object({
            phoneNumber,
            nationalCode,
         });
         return validationSchema;
      case 'login':
         validationSchema = Yup.object({
            phoneNumber,
            password,
         });
         return validationSchema;
      case 'get-code':
         validationSchema = Yup.object({
            code,
         });
         return validationSchema;
      case 'complete-profile':
         validationSchema = Yup.object({
            firstName,
            lastName,
            grade,
            password,
            confirmPassword,
         });
         return validationSchema;
      case 'change-password':
         validationSchema = Yup.object({
            password,
            confirmPassword,
         });
         return validationSchema;
      case 'forget-password':
         validationSchema = Yup.object({
            phoneNumber,
         });
         return validationSchema;
      default:
         return 'invalid data';
   }
};

export default useValidation;
